import React, { Fragment } from 'react';
import {
  Button,
  Grid,
  Container,
  FormControl,
  TextField, Typography,
  Tooltip,
  CircularProgress,
  Modal,
} from '@mui/material';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import styles from './steps.module.scss';
import textStyles from '../../../assets/stylesheets/text-styles.module.scss';
import { texts_simulate as texts } from '../../../global/texts/texts-simulate-page.js'

import { CustomBreadcrumbs } from '../breadcrumbs';
import { ppi } from '../../../helpers/logical-functions/runppi';

import * as d3 from 'd3';
import { psd3 } from '../steps/charts/psd3';
import * as am5 from '@amcharts/amcharts5';
import * as am5xy from "@amcharts/amcharts5/xy";
import am5themes_Animated from "@amcharts/amcharts5/themes/Animated";
import * as XLSX from 'xlsx';
import FileSaver from 'file-saver';
import {
  COLUMN_WIDTH, HIGHLIGHT,
  LABEL_COLOR, PERIOD,
  PIE_COLORS, ROTATE_LABELS,
  RUN_PPI_ITERATIONS, STROKE,
} from '../../../global/constants';
import PrintIcon from '@mui/icons-material/Print';
import { Box } from '@mui/system';
import { SiteTitle } from '../../../components/SiteTitle';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  maxWidth: 500,
  width: '100%',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
};

const runPPIs = (
  iterations, IF, alphas, alphas_prime, betas, A, R, bs, qm, rl,
  Imax, Imin, Bs, B_dict, G, period, frontier, test, forCalibrate
) => {
  const ppiResults = [];
  let rows = 0;
  let cols = 0;
  for (let iter = 0; iter < iterations; iter++) {
    const intermediateResult = ppi(IF, alphas, alphas_prime, betas, A, R, bs, qm, rl,
      Imax, Imin, Bs, B_dict, G, period, frontier, test, forCalibrate);
    [rows, cols] = intermediateResult[0].shape;
    ppiResults[iter] = intermediateResult[0].tolist();
  }
  let returnPPI = [];
  for (let r = 0; r < rows; r++){
    returnPPI.push([]);
    for (let c = 0; c < cols; c++) {
      returnPPI[r].push(0);
      ppiResults.forEach((ppiResult) => {
        returnPPI[r][c] += ppiResult[r][c];
      });
      returnPPI[r][c] = returnPPI[r][c] / iterations;
    }
  }
  // eslint-disable-next-line no-undef
  return nj.array(returnPPI);
}

/* LINE CHART */
const getLineData = (data, indicatorsResult) => {
  const [numberRows, numberPeriods] = data.shape;
  const indicatorsIds = indicatorsResult.ID;
  const indicatorsColors = indicatorsResult.color;
  const initialValues = indicatorsResult.IF;
  let localLineData = [];
  for (const iterRow in Array.from({ length: numberRows })) {
    let localDataSerie = [];
    for (const iterCol in Array.from({ length: numberPeriods })) {
      localDataSerie.push({
        name: indicatorsIds[iterRow],
        value: data.get(iterRow, iterCol) - initialValues[iterRow],
        color: indicatorsColors[iterRow],
        period: parseInt(iterCol),
      });
    }
    localLineData.push(localDataSerie);
  }
  return localLineData;
}
 /* PIE CHART */
const getPieData = (data, indicatorsResult) => {
  const [numberRows, numberPeriods] = data.shape;
  const indicatorsIds = indicatorsResult.ID;
  const indicatorsColors = indicatorsResult.color;
  const indicatorsNames = indicatorsResult.name;
  const initialValues = indicatorsResult.IF;
  const firstVariation = {
    id: "Negative",
    name: "Negative",
    color: PIE_COLORS.negative,
    pieValue: 0,
    indicatorValue: 0,
    variation: 0,
    drilldown: [],
  }
  const secondVariation = {
    id: "0 to 50%",
    name: "0 to 50%",
    color: PIE_COLORS.middle,
    pieValue: 0,
    indicatorValue: 0,
    variation: 0,
    drilldown: [],
  }
  const thirdVariation = {
    id: "More than 50%",
    name: "More than 50%",
    color: PIE_COLORS.higher,
    pieValue: 0,
    indicatorValue: 0,
    variation: 0,
    drilldown: [],
  }
  let totalValues = 0;
  for (const iterRow in Array.from({ length: numberRows })) {
    totalValues += !isNaN(data.get(iterRow, numberPeriods - 1))
      ? data.get(iterRow, numberPeriods - 1)
      : 0;
    const variation = ((data.get(iterRow, numberPeriods - 1) - initialValues[iterRow]) / Math.abs(initialValues[iterRow])) * 100;
    const drilldownData = {
      id: indicatorsIds[iterRow],
      name: indicatorsNames[iterRow],
      color: indicatorsColors[iterRow],
      indicatorValue: !isNaN(data.get(iterRow, numberPeriods - 1))
        ? data.get(iterRow, numberPeriods - 1)
        : 0,
      initialValue: !isNaN(initialValues[iterRow])
        ? initialValues[iterRow]
        : 0,
      variation: !isNaN(variation)
        ? variation
        : 0,
    };
    if (variation <= 0) {
      firstVariation.drilldown.push(drilldownData);
    } else if (variation <= 50){
      secondVariation.drilldown.push(drilldownData);
    } else {
      thirdVariation.drilldown.push(drilldownData);
    }
  }
  const pieData = [firstVariation, secondVariation, thirdVariation];
  pieData.forEach((pd, key) => {
    let localTotal = 0;
    pd.drilldown.forEach((d, key2) => {
      pieData[key].drilldown[key2].pieValue = pieData[key].drilldown[key2].indicatorValue * 100 / totalValues;
      localTotal += !isNaN(pieData[key].drilldown[key2].indicatorValue * 100 / totalValues)
        ? pieData[key].drilldown[key2].indicatorValue * 100 / totalValues
        : 0;
    });
    pieData[key].pieValue = localTotal;
    pieData[key].indicatorValue = localTotal;
    pieData[key].id = `${pieData[key].id} (${localTotal.toFixed(2).toString()}%)`
  });
  return pieData;
}

const getDownloadData = (data, indicatorsResult) => {
  const [numberRows, numberPeriods] = data.shape;
  const downloadData = [];
  for (const iterRow in Array.from({ length: numberRows })) {
    const col = [];
    for (const iterColumn in Array.from({ length: numberPeriods })) {
      col.push(data.get(iterRow, iterColumn));
    }
    downloadData.push(col);
  }
  const returnData = downloadData.map((d, key) => [indicatorsResult.ID[key], ...d]);
  return returnData;
}

const getVariationData = (data, indicatorsResult) => {
  const [numberRows, numberPeriods] = data.shape;
  const indicatorsIds = indicatorsResult.ID;
  const indicatorsColors = indicatorsResult.color;
  const indicatorsNames = indicatorsResult.name;
  const initialValues = indicatorsResult.IF;
  const localVariationData = [];
  for (const iterRow in Array.from({ length: numberRows })) {
    localVariationData.push({
      name: indicatorsIds[iterRow],
      value: initialValues[iterRow],
      valueNext: data.get(iterRow, numberPeriods - 1),
      color: indicatorsColors[iterRow],
    });
  }

  return localVariationData;
}


const Simulate = (props) => {
  const { simulationState, calibration, indicatorsResult, budgetResults, networkResults, budget } = props;
  const [simulateResult, setSimulateResult] = React.useState();
  const [period, setPeriod] = React.useState(PERIOD);
  const [loading, setLoading] = React.useState(false);
  const [pieData, setPieData] = React.useState([]);
  const [variationData, setVariationData] = React.useState([]);
  const [lineData, setLineData] = React.useState([]);
  const [doSimulation, setDoSimulation] = React.useState(false);
  const [open, setOpen] = React.useState(false);
  const [printGraph, setPrintGraph] = React.useState(0);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const { IF, R, qm, rl, Imax, Imin } = indicatorsResult;
  const A = networkResults !== null ? networkResults.A : null;
  let alphas = indicatorsResult.alpha || [];
  let alphas_prime = indicatorsResult.alpha_prime || [];
  let betas = indicatorsResult.beta || [];
  let T = [];
  let Bs = null;
  let B_dict = null;
  if (budgetResults !== null) {
    Bs = budgetResults.Bs;
    B_dict = budgetResults.B_dict;
  } else {
    Bs = budget;
  }
  if (calibration) {
    calibration.selection.data.forEach((d, key) => {
      if (key !== 0) {
        alphas[key] = d[1];
        alphas_prime[key] = d[2];
        betas[key] = d[3];
        T[key] = d[4];
      }
    });
    // Remove string value from the excel sheet
    alphas.shift();
    alphas_prime.shift();
    betas.shift();
  }

  const simulate = () => {
    return new Promise((resolve) => {
      setLoading(true);
      const localSimulateResult = runPPIs(RUN_PPI_ITERATIONS, IF, alphas, alphas_prime, betas, A, R, null, qm, rl,
        Imax, Imin, Bs, B_dict, null, period, null, false, false);
      resolve(localSimulateResult);

    }).then((result) => {
      setSimulateResult(result);
      setLineData(getLineData(result, indicatorsResult));
      setVariationData(getVariationData(result, indicatorsResult));
      setPieData(getPieData(result, indicatorsResult));
    });
  }

   var color = d3.scale.ordinal()
     .domain([1, 2, 3, 4])
     .range([PIE_COLORS.inner, PIE_COLORS.negative, PIE_COLORS.middle, PIE_COLORS.higher]);
    // Draw chart
  // eslint-disable-next-line no-undef
  let samplePie = null;
  React.useEffect(() => {
    if (pieData && pieData.length > 0) {
      var config = {
        containerId: "donutChartDiv",
        width: 600,
        height: 600,
        data: pieData,
        label: function (d) {
          return d.data.id
        },
        value: "pieValue",
        inner: "drilldown",
        tooltip: function (d) {
          return "<div style='background-color:" + d.color +"; color: white; padding: 10px; text-align: middle; border: solid 1px black;'><strong> ID: </strong>" +
           d.id + "<br> <strong>Value: </strong>" + d.indicatorValue.toFixed(2) + "</div>";
        },
        transition: "linear",
        transitionDuration: 100,
        donutRadius: 50,
        colors: color,
        labelColor: LABEL_COLOR,
        stroke: STROKE,
        strokeWidth: 1,
        drilldownTransition: "linear",
        drilldownTransitionDuration: 500,
        highlightColor: HIGHLIGHT,
        rotateLabel: ROTATE_LABELS,
      };
      if (samplePie) {
        samplePie.remove();
        samplePie = new psd3.Pie(config);
      } else {
        samplePie = new psd3.Pie(config);
      }
    }
  }, [pieData]);

  React.useEffect(() => {
    if (lineData && lineData.length > 0) {
      /* Line chart */
      let rootLine = am5.Root.new("lineChartDiv");;

      rootLine.setThemes([
        am5themes_Animated.new(rootLine)
      ]);

      let lineChart = rootLine.container.children.push(am5xy.XYChart.new(rootLine, {
        panX: true,
        panY: true,
        wheelX: "panX",
        wheelY: "zoomX",
        maxTooltipDistance: 0,
        pinchZoomX: true,
      }));

      let cursorLine = lineChart.set("cursor", am5xy.XYCursor.new(rootLine, {
        behavior: "none"
      }));
      cursorLine.lineY.set("visible", false);

      let xAxisLine = lineChart.xAxes.push(
        am5xy.ValueAxis.new(rootLine, {
          maxDeviation: 0,
          renderer: am5xy.AxisRendererX.new(rootLine, {}),
          tooltip: am5.Tooltip.new(rootLine, {}),
        })
      );

      let yAxisLine = lineChart.yAxes.push(am5xy.ValueAxis.new(rootLine, {
        renderer: am5xy.AxisRendererY.new(rootLine, {})
      }));

      xAxisLine.children.push(
        am5.Label.new(rootLine, {
          text: "Simulation period",
          x: am5.p50,
          centerX:am5.p50
        })
      );
      
      yAxisLine.children.unshift(
        am5.Label.new(rootLine, {
          rotation: -90,
          text: "Indicator progress",
          y: am5.p50,
          centerX: am5.p50
        })
      );

      const [numberRows, numberPeriods] = simulateResult.shape;
      for (const iterRow in Array.from({ length: numberRows })) {
        let seriesLine = lineChart.series.push(am5xy.LineSeries.new(rootLine, {
          name: "Series " + iterRow,
          xAxis: xAxisLine,
          yAxis: yAxisLine,
          valueYField: "value",
          valueXField: "period",
          legendValueText: "{valueY}",
          fill: am5.color(indicatorsResult.color[iterRow]),
          stroke: am5.color(indicatorsResult.color[iterRow]),
          tooltip: am5.Tooltip.new(rootLine, {
            pointerOrientation: "horizontal",
            labelText: '[bold]ID: [/]{name}\n[bold]Value: [/]{valueY}',
          })
        }));
        seriesLine.data.setAll(lineData[iterRow]);

        // Make stuff animate on load
        // https://www.amcharts.com/docs/v5/concepts/animations/
        seriesLine.appear();
      }
      lineChart.appear(1000, 100);
    }
  }, [lineData]);

  React.useEffect(() => {
    if (variationData && variationData.length > 0) {
      /* Variation chart */
      let root = am5.Root.new("barChartDiv");
      root.setThemes([
        am5themes_Animated.new(root)
      ]);

      let chart = root.container.children.push(am5xy.XYChart.new(root, {
        panX: true,
        panY: true,
        wheelX: "panX",
        wheelY: "panY",
        layout: root.verticalLayout
      }));
      var xRenderer = am5xy.AxisRendererX.new(root, { minGridDistance: 5 });
      xRenderer.labels.template.setAll({
        rotation: -90,
        fontSize:14,
        centerX: am5.p100,
        dx: -18
      });
      let xAxis = chart.xAxes.push(am5xy.CategoryAxis.new(root, {
        categoryField: "name",
        renderer: xRenderer,
        maxDeviation: 0.3,
        tooltip: am5.Tooltip.new(root, {})
      }));

      xAxis.data.setAll(variationData);

      let yAxis = chart.yAxes.push(am5xy.ValueAxis.new(root, {
        min: 0,
        renderer: am5xy.AxisRendererY.new(root, {})
      }));

      xAxis.children.push(
        am5.Label.new(root, {
          text: "Indicator",
          x: am5.p50,
          centerX:am5.p50
        })
      );
      
      yAxis.children.unshift(
        am5.Label.new(root, {
          rotation: -90,
          text: "Indicator progress change",
          y: am5.p50,
          centerX: am5.p50
        })
      );

      let series = chart.series.push(am5xy.ColumnSeries.new(root, {
        xAxis: xAxis,
        yAxis: yAxis,
        valueYField: "value",
        categoryXField: "name",
      }));

      series.columns.template.setAll({
        tooltipText: "{categoryX}: {valueY}",
        width: am5.percent(COLUMN_WIDTH),
        tooltipY: 0,
      });

      series.data.setAll(variationData);
      series.columns.template.setAll({ cornerRadiusTL: 5, cornerRadiusTR: 5 });
      series.columns.template.adapters.add("fill", function (fill, target) {
        return target.dataItem.dataContext.color;
      });

      series.columns.template.adapters.add("stroke", function (stroke, target) {
        return target.dataItem.dataContext.color;
      });
      // Variance indicator series
      let series2 = chart.series.push(am5xy.ColumnSeries.new(root, {
        xAxis: xAxis,
        yAxis: yAxis,
        dx: -12,
        valueYField: "valueNext",
        openValueYField: "value",
        categoryXField: "name",
        fill: am5.color(0x000000),
        stroke: am5.color(0x000000),
      }));

      // arrow line width
      series2.columns.template.setAll({
        width: 1
      });
      series2.data.setAll(variationData);

      series2.bullets.push(function () {
        let arrow = am5.Graphics.new(root, {
          rotation: -90,
          dx: -12,
          dy: 3,
          fill: am5.color(0x000000),
          draw: function (display) {
            display.moveTo(0, -3);
            display.lineTo(8, 0);
            display.lineTo(0, 3);
            display.lineTo(0, -3);
          }
        });

        arrow.adapters.add("rotation", function (rotation, target) {
          return getVariancePercent(target.dataItem) < 0 ? 90 : rotation;
        });

        arrow.adapters.add("dy", function (dy, target) {
          return getVariancePercent(target.dataItem) < 0 ? -3 : dy;
        });

        return am5.Bullet.new(root, {
          locationY: 1,
          sprite: arrow
        })
      })


      // Make stuff animate on load
      // https://www.amcharts.com/docs/v5/concepts/animations/
      series.appear(1000);
      chart.appear(1000, 100);

      function getVariancePercent(dataItem) {
        if (dataItem) {
          let value = dataItem.get("valueY");
          let openValue = dataItem.get("openValueY");
          let change = value - openValue;
          return Math.round(change / openValue * 100);
        }
        return 0;
      }
    }
  }, [variationData]);

  const downloadExcel = (dataArray) => {
    const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
    const fileExtension = '.xlsx';
    const fileName = `SimulationData-${new Date().toLocaleDateString().replaceAll('/', '-')}`;
    const worksheet = XLSX.utils.aoa_to_sheet(dataArray);
    const newWorkbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(newWorkbook, worksheet, 'SheetJS');
    const excelBuffer = XLSX.write(newWorkbook, { bookType: 'xlsx', type: 'array' });
    const data = new Blob([excelBuffer], { type: fileType });
    FileSaver.saveAs(data, fileName + fileExtension);
  };

  React.useEffect(() => {
    if (printGraph !== 0) {
      window.print();
    }
  }, [printGraph]);

  const printClick = (graph) => {
    setPrintGraph(graph);
  }

  React.useEffect(() => {
    if (loading) {
      setDoSimulation(true);
    }
  }, [loading]);

  React.useEffect(() => {
    const onClickSimulate = async () => {
      simulate();
    };
    if (doSimulation === true) {
      onClickSimulate();
      setLoading(false);
      setDoSimulation(false);
    }
  }, [doSimulation]);


  return (
    <Container
      maxWidth="xl"
      sx={{
        paddingY: (theme) => [ theme.spacing(4), theme.spacing(4), theme.spacing(8) ]
      }}
    >
      <Grid container rowSpacing={4} columnSpacing={8} columnns={{ xs: 4, sm: 8, md: 12, lg: 12 }}>
        {!simulateResult && (
          <>
            <Grid item xs={12} md={5}>
              <SiteTitle />
            </Grid>
            <Grid item xs={12} md={7}>
              <CustomBreadcrumbs selected={simulationState} />
            </Grid>
            <Grid item xs={12} md={5}>
              <Typography variant='h1'>
                Simulation
              </Typography>
              <Typography mt={4}>
                { texts.INFORMATION_DESCRIPTION_FIRST }
              </Typography>
              <Typography mt={4}>
                { texts.INFORMATION_DESCRIPTION_SECOND }
              </Typography>
            </Grid>
          </>
        )}
        <Grid item xs={12} md={simulateResult ? 12 : 7}>
          {!simulateResult
            && (
              <>
                <Typography variant='body1' mb={4}>
                  { texts.NUMBER_OF_PERIODS_DESCRIPTION }
                </Typography>
                  <Grid container spacing={4} columnns={{ xs: 4, sm: 8, md: 12, lg: 12 }}>
                    <Grid item xs={12} md={6}>
                      <FormControl fullWidth variant="standard">
                        <TextField
                          id="period"
                          variant="outlined"
                          value={period}
                          label="Number of periods to simulate"
                          onChange={(e) => setPeriod(Number(e.target.value))}
                          placeholder="1 - 1000"
                          disabled={loading}
                        />
                    </FormControl>
                  </Grid>
                  <Grid item xs={12} md={6}>
                    {!loading && (
                      <Button
                        variant="contained"
                        size="large"
                        onClick={() => handleOpen(true)}
                        disabled={period <= 0 || period >= 1000 || loading}
                      >
                        Start simulation
                      </Button>
                    )}
                  </Grid>
                </Grid>
              </>
          )}
          {!simulateResult && (
            <Modal
              open={open}
              onClose={handleClose}
              aria-labelledby="modal-modal-title"
              aria-describedby="modal-modal-description"
            >
              <Box sx={style}>
                <Box p={4}>
                  <Typography id="modal-modal-title" variant="h5" component="h2" mb={2}>
                    Information before you start
                  </Typography>
                  <Typography id="modal-modal-description" mb={2}>
                    The simulation will use much of your computer's resources and other tasks may be affected while the simulation runs. We suggest not attempting to perform other tasks while it is running.
                  </Typography>
                  <Typography mb={4}>
                    Are you sure you want to run the simulation now?
                  </Typography>
                  {!loading && (
                    <Grid container spacing={4} columnns={{ xs: 4, sm: 8, md: 12, lg: 12 }}>
                      <Grid item xs={12} lg={6}>
                        <Button
                          variant='contained'
                          size='large'
                          onClick={() => setLoading(true)}
                          disabled={period <= 0 || period >= 1000 || loading}
                        >
                          Start simulation
                        </Button>
                      </Grid>
                      <Grid item xs={12} lg={6}>
                        <Button
                          variant='outlined'
                          size='large'
                          onClick={handleClose}
                          disabled={period <= 0 || period >= 1000 || loading}
                        >
                          Not right now
                        </Button>
                      </Grid>
                    </Grid>
                  )}
                  {loading
                    && (
                      <Fragment>
                        <CircularProgress color="success" />
                        <Typography className={[textStyles.subtitle].join(' ')}>
                          Simulating...
                        </Typography>
                      </Fragment>
                    )}
                </Box>
              </Box>
            </Modal>
          )}
          {!loading
          && simulateResult
          && (
            <>
                <Box sx={{ textAlign: 'right' }} className={styles.noPrint}>
                  <Button
                    variant='contained'
                    onClick={() => downloadExcel(getDownloadData(simulateResult, indicatorsResult))}
                    >
                      Download calibrated data
                  </Button>
                  <Button
                    variant='contained'
                    sx={{ marginLeft: (theme) => theme.spacing(2) }}
                    onClick={() => window.print()}
                  >
                    <PrintIcon />
                  </Button>
                </Box>

                <div className={printGraph !== 1 && styles.noPrint}>
                  <Typography variant="h4" mb={2} mt={6}>
                    { texts.CHART_PROGRESS_TITLE }
                    <span className={styles.noPrint}>
                      <Tooltip sx={{ marginLeft: (theme) => theme.spacing(2) }} title={ texts.CHART_PROGRESS }>
                        <InfoOutlinedIcon />
                      </Tooltip>
                      <Button
                        onClick={() => printClick(1)}
                      >
                        <PrintIcon />
                      </Button>
                    </span>
                  </Typography>
                  <div id="lineChartDiv" className={styles.chart} style={{ height: '500px', width: '100%' }}/>
                </div>

                <div className={printGraph !== 2 && styles.noPrint}>
                  <Typography variant="h4" mb={2} mt={6}>
                    { texts.CHART_BAR_TITLE }
                    <span className={styles.noPrint}>
                      <Tooltip sx={{ marginLeft: (theme) => theme.spacing(2) }} title={ texts.CHART_DONUT }>
                        <InfoOutlinedIcon />
                      </Tooltip>
                      <Button
                        onClick={() => printClick(2)}
                      >
                        <PrintIcon />
                      </Button>
                    </span>
                  </Typography>

                  <div className={styles.chart} id="barChartDiv" style={{ height: '500px', width: '100%' }} />
                </div>

                <div className={printGraph !== 3 && styles.noPrint}>
                  <Typography variant="h4" mb={2} mt={6}>
                    { texts.CHART_DONUT_TITLE }
                    <span className={styles.noPrint}>
                      <Tooltip sx={{ marginLeft: (theme) => theme.spacing(2) }} title={ texts.CHART_BAR } >
                        <InfoOutlinedIcon />
                      </Tooltip>
                      <Button
                        onClick={() => printClick(3)}
                      >
                        <PrintIcon />
                      </Button>
                    </span>
                  </Typography>
                  <div id="donutChartDiv" />
                </div>
              </>
          )}
        </Grid>
      </Grid>
    </Container>
  )
}

export { Simulate };

export default Simulate;