const NUMBER_OF_PERIODS_DESCRIPTION = 'Choose the number of periods that you wish to simulate. As a reference, the calibration step uses 50 periods. Therefore, if your budget data represent the average annual expenditure of 10 years, there is an equivalence of 10 years = 50 simulation periods. Accordingly, for example, if you wish to simulate 5 years, you will need to input 25 periods in the field. You should avoid simulating too many periods, as PPI is a short-term oriented tool. Hence, the app will limit your input to a maximum of 1000 simulation periods.';

const INFORMATION_DESCRIPTION_FIRST = 'Now, you are all set to run simulations with PPI. By default, PPI uses the final values of your indicators (the column "final_value" in the indicators template) as the initial conditions for the simulations (because they are meant to be prospective). If you would like to change these initial conditions, you need to re-run the app uploading a new indicators file with the parameters already calibrated, and alternative values in the column "final_value". The same logic applies if you would like to run counterfactual simulations with an alternative budget.';

const INFORMATION_DESCRIPTION_SECOND = 'When you run PPI, the app performs a set of independent Monte Carlo simulations in the background. The results are the average values of each indicator in each step across these independent simulations. Hence, this step yields the expected trajectories of the indicators. Once the simulations finish, the app displays three charts for quick analysis, and enables you to download the expected time series for further analysis.';

const CHART_PROGRESS_TITLE = 'Net progress of the indicators';

const CHART_PROGRESS = 'This chart shows the difference between the simulated values the indicators and their initial values. Hence, values above zero mean that the indicator improves, while negative values indicators worsening.';

